import React ,{Component}from 'react';
import {NavLink} from 'react-router-dom';
const navstyle={
    color:'rgb(171,92,62)',
    borderBottom: '7px solid rgb(171,92,62)'

}
class Qydt_nav extends Component{
    render(){
        return(
            <div className='qydt-nav'>
            <NavLink id='qydt-nav1' style={navstyle} activeClassName='qydt-nav-active' to='/qiyedongtai/news' >行业资讯</NavLink>
            <NavLink id='qydt-nav2'  activeClassName='qydt-nav-active' to='/qiyedongtai/Honor/honor1'>企业荣誉</NavLink>
            <NavLink id='qydt-nav3'   activeClassName='qydt-nav-active' to='/qiyedongtai/corporateimage'>企业形象</NavLink>
        </div>
    )
    }
}
export default Qydt_nav;


