//行业资讯
import React  from 'react';
import {Link} from 'react-router-dom'
import Qydt_nav from '../../../components/CN/企业动态/Qydt_nav'



class Qydt1 extends React.PureComponent{
    componentWillMount(){
        document.title='行业资讯-企业动态-飞遍全球国际旅游集团官方网站';
    }
    constructor(){
        super();
        this.state={title:'',time:''}
        
    }
    
    
    render(){
        return(
       <div>
           <Qydt_nav/>  
    <div className='qydt-content'>
        <div className='qydt-c'>
          <Link id='' to='/qiyedongtai/news1' id=''>  <div className='qydt-img-ifm1'></div></Link>
          <Link id='' to='/qiyedongtai/news1' id=''>   <div className='qydt-font'><h1>成都直飞特拉维夫航线将开通,每周往返各两班</h1><p>一条新的国际直飞航线即将拉近成都与以色列之间的距离。昨日，记者从市口岸与物流办、川航等相关单位获悉，成都直飞以色列第二大城市——特拉维夫航线计划于8月
中旬开通，届时 成都有望成为中国内地第三个、中西部地区首个直航以色列的城市。该航线开通将极大地促进我国中西部地区与以色列之间的经贸合作和交流往来...
 </p></div></Link>
            <div className='qydt-time-c'><div> <h1>05-28</h1><h2>2018</h2> </div><Link id='' to='/qiyedongtai/news1'>查看更多</Link></div>
        </div>
        <div className='qydt-c'>
        <Link id='' to='/qiyedongtai/news2' id=''> <div className='qydt-img-ifm2'></div></Link>
        <Link id='' to='/qiyedongtai/news2' id=''>    <div className='qydt-font'>
        <h1>从俄罗斯到卡塔尔，卡塔尔航空要做世界杯的连接者</h1>
        <p>卡塔尔航空作为FIFA国际足联官方合作伙伴及官方航空公司，在世界杯开赛前，特别准备了一支激情澎湃的全新广告宣传片，并联手歌星及电视明星Nicole Scherzinger重新
录制经典歌曲“Dancing in the Streets”这首振奋人心的歌曲体现了卡塔尔航空对体育精神的坚定信念：体育运动是世界语言,它鼓舞人们勇敢追逐自己的梦想...</p>
        </div></Link>
                <div className='qydt-time-c'> <div><h1>06-18</h1><h2>2018</h2></div><Link  to='/qiyedongtai/news2'>查看更多</Link></div>
        </div>
        <div className='qydt-c'>
        <Link id='' to='/qiyedongtai/news3' > <div className='qydt-img-ifm3'></div></Link>
        <Link id='' to='/qiyedongtai/news3' > 
        <div className='qydt-font'>
        <h1>A350助飞熊猫之路，四川航空开启“熊猫航线”!</h1>
        <p>四川航空首架A350熊猫飞机将于2018年8月9日到达成都！8月16日进行商业首航。四川航空起飞三十载，从运七飞机探蜀道之难，到如今全空客机队铺就“熊猫之路”，一
步一印筑通途。270余条“熊猫航线”通达亚洲、欧洲、大洋洲和北美洲地区，在服务好国内需求的基础上，向“全服务、网络型、国际化”进一步转型升级...</p>
        </div></Link>
                <div className='qydt-time-c'> <div> <h1>07-08</h1><h2>2018</h2></div><Link id='' to='/qiyedongtai/news3'>查看更多</Link></div>
        </div>
        <div className='qydt-c'>
          <Link id='' to='/qiyedongtai/news4'>  <div className='qydt-img-ifm4'></div></Link>
          <Link id='' to='/qiyedongtai/news4'>
             <div className='qydt-font'>
             <h1>摩洛哥免签吸引了更多华人，促进文化交流</h1>
             <p>位于非洲西北部的摩洛哥是旅游大国，每年这里都会吸引大量外国游客。自2016年6月1号开始，摩洛哥对中国游客实行免签政策。如今，将近一年过去了，来摩洛哥旅游和
经商的中国人数量不断增加，给摩洛哥旅游业的发展增添了新的活力...</p>
             </div></Link>
            <div className='qydt-time-c'><div> <h1>07-18</h1><h2>2018</h2> </div><Link id='' to='/qiyedongtai/news4'>查看更多</Link></div>
        </div>
        <div className='qydt-c5'>
        <Link id='' to='/qiyedongtai/news5'> <div className='qydt-img-ifm5'></div></Link>
        <Link id='' to='/qiyedongtai/news5'>    <div className='qydt-font'>
        <h1>2018开启土耳其旅游年</h1>
        <p> 2017年传遍大街小巷的《带你去旅行》，热门歌词“我想要带你去浪漫的土耳其”唤起了不少人的土耳其旅游梦，国内的各大真人秀节目如《花儿与少年》、《二十四小时》
等纷纷远赴土耳其取景。由于一带一路政策的推行，2018年更是直接成为中土旅游年.赴土耳其游玩的中国游客福利增多。首先，自今年3月开始，中国公民申请土耳其电子签，可以使用支付宝进行支付，最快15分钟就能搞定土耳
其签证...</p></div></Link>
                <div className='qydt-time-c'> <div><h1>07-22</h1><h2>2018</h2></div><Link id='' to='/qiyedongtai/news5'>查看更多</Link></div>
        </div>
        
    </div>
    </div>
        )
    }
    }
    export default Qydt1;
    



