import React from 'react';
import {Link} from 'react-router-dom';
import $ from 'jquery';
import News_nav from '../../../components/CN/企业动态/News_nav';
class Corporateimage1 extends React.PureComponent{
    componentWillMount(){
        document.title='飞遍全球航旅集团的历程发展和未来方向-企业动态-飞遍全球国际旅游集团官方网站';
        


    }
    componentDidMount(){
        $(window).scrollTop(600)
    }
    
      render(){
          return(
            <div className='news-c'>
    <News_nav/>
    <div className='news-content-c'>
    <div className='news-content'>
        <h1>飞遍全球航旅集团的历程发展和未来方向</h1>
        <h2> 发布时间2018.06.06   内容来源：官方网站</h2>
        <p> 飞遍全球国际旅游集团 是中国（四川）自由贸易试验区设立以来注册的首家国际旅游集团企业，总部位于成都金融城。飞遍全球旅游集团以中国西部大开发为背景，在国家
“一带一路”提倡的指引下，从北京到成都，李历经七年的沉淀和发展，现已发展成集团国际航空、国际旅游和在线航旅三位一体、全新业态的国际旅游集团企业。飞遍全球国
际旅游集团作为国际旅游产业链上游服务企业，一直致力于为客户提供专业的国际航空出行解决方案。在国际航空领域，凭借全球化视野，前瞻性的战略布局饿和专业化的服务
水准，已为中国西部地区超过30万名游客提供出行服务。</p>
        <div className='corporate-img-1-1'><img src=''/></div>
        <p> 飞遍全球国际旅游集团率先提出“品质出行”倡议，以产品的品质为核心，不断提高产品的设计能力和服务能力，先后开辟四川、重庆、贵州、云南、陕西、宁夏、甘肃等中
国西部旅游市场。并逐步发展成为中国西部地区领先的中高端国际旅游服务企业。飞遍全球国际旅游集团将实行以成都为中心，面向全国的发展战略。加大对国际航空和国际旅
游资源的整合力度，以丰富和完善中国国际旅游上游产业链为己任，搭建起中国通往世界的桥梁，让中国走向世界，让世界了解中国！</p>
       
        <div className='corporate-img-1-2'><img src=''/></div>
            
             
        <p>飞遍全球国际旅游集团，中国国际旅游品质出行倡导者！</p>
        <div className='corporate-img-1-3'><img src=''/></div>
        </div>
  
    </div>
    <div className='news-foot'>
        <div>
        <span>上一篇：</span>
        <Link to='/qiyedongtai/Honor/honor1' id=''><span>&nbsp;飞遍全球旅游集团获卡塔尔航空“最佳代理人”</span></Link>
        </div>
        <div>
        <span>下一篇：</span>
        <Link to='/qiyedongtai/corporateimage2' id=''> <span>飞遍全球旅游集团董事长亲自考察象岛旅游项目</span></Link>
        </div>
    </div>
            </div>
           
          )
      }
}

export default Corporateimage1;


