//摩洛哥
import React from 'react';
import {Link} from 'react-router-dom'
import $ from 'jquery'
import News_nav from '../../../components/En/企业动态/News_nav';

class News4 extends React.PureComponent{
    componentWillMount(){
       
        document.title='摩洛哥免签吸引了更多华人，促进文化交流-企业动态-飞遍全球国际旅游集团官方网站';
    }

    componentDidMount(){
        $(window).scrollTop(600)
    }
      render(){
          return(
            <div className='news-c'>
   <News_nav/>
   <div className='news-content-c'>
    <div className='news-content'>
        <h1>摩洛哥免签吸引了更多华人，促进文化交流</h1>
        <h2>发布时间2018.07.18   内容来源：官方网站 </h2>
        <p> 位于非洲西北部的摩洛哥是旅游大国，每年这里都会吸引大量外国游客。自2016年6月1号开始，摩洛哥对中国游客实行免签政策。如今，将近一年过去了，来摩洛哥旅游和
经商的中国人数量不断增加，给摩洛哥旅游业的发展增添了新的活力。摩洛哥对中国游客实行免签政策后，中国游客人数的增幅甚至超过了摩洛哥政府的预期，原定的目标是
2018年吸引10万中国游客，而这个数字已经提前实现。随着两国直航航线的开通，摩洛哥国家旅游局局长对未来中国游客人数的进一步增长，更是充满期待。</p>
        <div className='news-img-4-1'></div>
        <p> 摩洛哥国家旅游局局长  阿卜杜拉费耶·祖亭：今年中国游客人数将超过10万人，从过去的2万人到现在的10万人，增长非常迅速，我们希望随着两国直飞航班的开通，来摩
洛哥的中国游客人数，能很快达到每年100万人。免签政策的施行，不但为摩洛哥的旅游业增添新气象，为中国游客来摩洛哥游玩提供了便利，也极大促进了两国间的人文交流。</p>
        <div className='news-img-4-2'><img src=''/></div>
        <p>中国驻摩洛哥大使馆文化参赞 石岳文：（免签）极大方便了中国的游客和中国不少的公务团体到这里访问，中国来的各方面文化艺术团体非常多，比如说，去年到摩洛哥来
的，通过我的官方途径，掌握到的艺术交流团体（数量），就达到了13个，人数达到400多人。</p>
        <div className='news-img-4-3'><img src=''/></div>
    </div>
  
            </div>
            <div className='news-foot'>
        <div>
        <span>上一篇：</span>
        <Link to='/en/qiyedongtai/news3'><span> &nbsp;A350助飞熊猫之路，四川航空开启“熊猫航线”!</span></Link>
        </div>
        <div>
        <span>下一篇：</span>
        <Link to='/en/qiyedongtai/news5'> <span>2018开启土耳其旅游年!</span></Link>
        </div>
    </div>
            </div>
          )
      }
}

export default News4;


