import React, { PureComponent } from 'react';
import {Link} from 'react-router-dom'
import $ from 'jquery'
import News_nav from '../../../components/CN/企业动态/News_nav';

class Honor1 extends React.PureComponent{
    componentWillMount(){
        document.title='飞遍全球旅游集团获卡塔尔航空“最佳代理人”-企业荣誉-企业动态-飞遍全球国际旅游集团官方网站';
    }
    componentDidMount(){
        $(window).scrollTop(600)
    }
      render(){
          return(
            <div className='news-c'>
           
    <News_nav/>
    <div className='news-content-c'>
    <div className='news-content'>
        <h1>飞遍全球旅游集团获卡塔尔航空“最佳代理人”</h1>
        <h2>发布时间2018.06.02   内容来源：官方网站</h2>
        <p> 2017年年底卡塔尔航空最佳代理颁奖典礼上，我司以机票销售评选为北亚区“最佳代理人”，过去的一年在团队的努力下，我们的业绩和服务得到了卡塔尔航空和客人的认
可，我们要做的不止是这些，在接下来的每一年，我们将以这个目标再接再厉，为全国各地的数百家中小型旅行社提供专业国际机票服务，满足数万中高端人士的出行需求。我
们有着优质的专业国际机票资源可以帮助中小旅行社提升市场份额和产品竞争力，有利于中小旅行社的快速成长。</p>
        <div className='honor-img-1-1'></div>
        <p>荣誉和付出的努力是成正比的，飞遍全球旅游集团董事长在参加完颁奖典礼后，接受卡塔尔航空的邀请，对卡塔尔首都多哈进行考察，卡塔尔不止有丰富的物质资源，当地
文化底蕴也是极其丰厚，董事长考察回国后定下目标，2018年做出更好的成绩，将带领团队一起感受多哈异国风情。 </p>
        <div className='honor-img-1-2'><img src=''/></div>
   
     
    </div>
    </div>
    <div className='news-foot'>
        <div>
        <span>上一篇：</span>
        <Link to='/qiyedongtai/news5'id=''><span>&nbsp;2018开启土耳其旅游年!</span></Link>
        </div>
        <div>
        <span>下一篇：</span>
        <Link to='/qiyedongtai/corporateimage1'id=''> <span>飞遍全球航旅集团的历程发展和未来方向</span></Link>
        </div>
    </div>
  
            </div>
          )
      }
}

export default Honor1;


