//卡塔尔航空
import React, { Component } from 'react';
import {Link} from 'react-router-dom';
import $ from 'jquery';
import News_nav from '../../../components/En/企业动态/News_nav';

class News2 extends React.PureComponent{
    componentWillMount(){
        document.title='从俄罗斯到卡塔尔，卡塔尔航空要做世界杯的连接者-新闻动态-飞遍全球国际旅游集团官方网站';
    }
    componentDidMount(){
        $(window).scrollTop(600)
    }
      render(){
          return(
            <div className='news-c'>
    <News_nav/>
    <div className='news-content-c'>
    <div className='news-content'>
        <h1>从俄罗斯到卡塔尔，卡塔尔航空要做世界杯的连接者</h1>
        <h2> 发布时间2018.06.18    内容来源：官方微信公众号</h2>
        <p> 卡塔尔航空作为FIFA国际足联官方合作伙伴及官方航空公司，在世界杯开赛前，特别准备了一支激情澎湃的全新广告宣传片，并联手歌星及电视明星Nicole Scherzinger重新
录制经典歌曲“Dancing in the Streets”。</p>
        <div className='video'></div>
        <p> 这首振奋人心的歌曲体现了卡塔尔航空对体育精神的坚定信念：体育运动是世界语言,它鼓舞人们勇敢追逐自己的梦想！也希望全世界的球迷能通过卡航的这段广告宣传片，
感受到到欢乐的气氛，与卡航一同迎接世界杯的到来，尽情享受这场足球盛事！</p>
       <div className='news-img-c1'>
            <div className='news-img-2-1'><img src=''/></div> 
            <div className='news-img-2-2'><img src=''/></div>
             </div>
        <p>2017年5月，卡航宣布与国际足联达成了一项突破性的赞助协议。卡航将作为国际足联的官方合作伙伴和官方航空公司直至2022年，拥有在2018俄罗斯世界杯™和2022年
卡塔尔世界杯™比赛中的营销和品牌权利。</p>
        <div className='news-img-2-3'><img src=''/></div>
        <p>随着法国在卢日尼基体育场捧杯，世界杯也宣告进入卡塔尔时间。国际足联此前已经确定，卡塔尔世界杯将在冬天进行，卡塔尔首相兼内政部大臣阿卜杜拉则表示，卡塔尔
将按照国家规划，全面完成开发项目和基础设施项目建设，特别是与卡塔尔世界杯相关联的项目建设，保证办好世界杯赛事。无论对于俄罗斯还是卡塔尔，世界杯都是国家工
程，那些国家品牌需要表明立场。俄罗斯有Gazprom，而卡塔尔则有卡塔尔航空。</p>
<div className='news-img-c'>
    <div className='news-img-2-4'><img src=''/></div>
    <div className='news-img-2-5'><img src=''/></div>
    </div>
<p>在半决赛和决赛开始前，卡塔尔航空将其独有的庆祝方式带到天空，一架带有国际足联特定形象的手绘波音777飞机被派往莫斯科。这架定制飞机的手绘工作是在爱尔兰玩
完成的，它展示了这家世界知名航空公司支持国际足联和世界杯的方式，同时也向全世界球迷传达了卡塔尔对于世界杯和球迷的欢迎。</p>
<div className='news-img-2-6'><img src=''/></div>
<p>卡塔尔航空首席执行官阿克巴·阿尔贝克尔（Akbar al-Baker）表示，成为世界杯这样大型体育赛事的一部分对于卡塔尔航空非常重要，世界杯将全世界热爱足球和体育的人
团结在一起。“我们被世界各地球迷在每场比赛中表现出的巨大热情所鼓舞。”卡塔尔航空高级副总裁萨拉姆·阿尔沙瓦（Salam al-Shawa）则认为，俄罗斯世界杯的成功让卡
塔尔航空对四年后的本土世界杯期待倍增。</p>
<div className='news-img-2-7'><img src=''/></div>
    </div>
    
            </div>
            <div className='news-foot'>
        <div>
        <span>上一篇：</span>
        <Link to='/en/qiyedongtai/news1'><span>&nbsp;成都直飞特拉维夫航线将开通,每周往返各两班</span></Link>
        </div>
        <div>
        <span>下一篇：</span>
        <Link to='/en/qiyedongtai/news3'> <span>A350助飞熊猫之路，四川航空开启“熊猫航线”!</span></Link>
        </div>
    </div>
            </div>
          )
      }
}

export default News2;


