//轮播组件
import React, { Component } from 'react';
import {Link} from 'react-router-dom';



class Carousel3 extends Component {
    render (){
        return(
          
        <section  id='jtcy-carousel'>
        <div id="myCarousel-jtcy" className="carousel slide">
        <ol className="carousel-indicators" >
        <li  data-target="#myCarousel-jtcy" data-slide-to="0" className="active"></li>
        <li   data-target="#myCarousel-jtcy" data-slide-to="1"></li>
        <li   data-target="#myCarousel-jtcy" data-slide-to="2"></li>
         <li  data-target="#myCarousel-jtcy" data-slide-to="3"></li>
         <li  data-target="#myCarousel-jtcy" data-slide-to="4"></li>
    </ol>
    {/*轮播项目*/}
    <div className="lunbotu">
    <div className="carousel-inner">
        
     <div  className=" item active" id='International shipping' >
     <Link to='/jituanchanye' id='International shipping' >
      <div className='carousel-jtcy1'>
      <h3 id='International shipping'>国际航空</h3>
      </div>
       
        
        </Link>
      </div>
      
      <div className=" item " id='carousel-jtcy'>
      <Link to='/jituanchanye' id='International tourism'  className='carousel-jtcy2'>
      <div className='carousel-jtcy2'></div>
       
   
      
      </Link>
      </div>
       <div  className="item" id='carousel-jtcy'>
     
       <Link to='/jituanchanye' id='International exhibition'  className='carousel-jtcy3'>
       <div className='carousel-jtcy3'></div> 
      
        </Link>
      </div> 
     <div  className="item " id='carousel-jtcy'>
     
     <Link to='/jituanchanye' id='Online voyage'  className='carousel-jtcy4'>
     <div className='carousel-jtcy4'></div>
     
      </Link>
      </div> 
      <div  className="item" id='Online voyage'>
     
      <Link to='/jituanchanye' id='Cruise mall'  className='carousel-jtcy5'> 
      <div className='carousel-jtcy5'></div>
      
      </Link>
     </div>
      </div>
 
      </div>
      </div>
      </section>
      
      )
    }
}
export default Carousel3;


