//页底组件
import React from 'react';
import {Link} from 'react-router-dom';
import xinlang from '../../image/xinlang.svg'
import weixin from '../../image/weixin.svg';
import $ from 'jquery'
 class Footer_en extends React.PureComponent {
     componentDidMount(){
         $('.list-c').on('click' ,'h1',function(){
            $(window).scrollTop(50);
         })
         $('.list-c1').on('click' ,'h1',function(){
            $(window).scrollTop(50);
         })
         $('.list-c1').on('click' ,'li',function(){
            $(window).scrollTop(1600);
         })
         $('.list-c').on('click' ,'li',function(event){
             let target=event.target;
             switch (target.id) {
                 case 'International shipping':
                 $(window).scrollTop(600)
                 break;
                 case 'International tourism':
                 $(window).scrollTop(1200)
                 break;
                 case 'International exhibition':
                 $(window).scrollTop(1800)
                 break;
                 case 'Online voyage':
                 $(window).scrollTop(2500)
                 break;
                 case 'Cruise mall':
                 $(window).scrollTop(3200)
                 break;
                 case 'Airlines':
                 $(window).scrollTop(1000)
                 break;
                 case 'Tourism Bureau':
                 $(window).scrollTop(1800);
                 break;
                 case 'Hotel group':
                 $(window).scrollTop(2900)
                 break;
                 default:;
             }
            
         })
        
     }
     render (){
         return(
<footer className='footer' id='footer-en'>
            <div className='footer-c' id='footer-c-en'>
        <div className='list-c-c'> 
        <ul className='list-c' id=''>    
            <h1><Link to='/en/jituanchanye' id='Group industry'>Group Industry</Link></h1>
            <li><Link to='/en/jituanchanye' id='International shipping'>International aviation</Link></li>
            <li><Link to='/en/jituanchanye' id='International tourism'>International tourism</Link></li>
            <li><Link to='/en/jituanchanye' id='International exhibition'>International exhibition</Link></li>
            <li><Link to='/en/jituanchanye' id='Online voyage'>Online voyage</Link></li>
            <li><Link to='/en/jituanchanye' id='Cruise mall'>Air travel MALL</Link></li>
        </ul>
        <ul className='list-c' >
           <h1><Link to='/en/zhanluehuoban' id=''>Partner</Link></h1>
           <li><Link to='/en/zhanluehuoban' id='Tourism Bureau'>Tourism bureau</Link></li>
           <li><Link to='/en/zhanluehuoban' id='Airlines'>Airlines</Link></li>
           <li><Link to='/en/zhanluehuoban' id='Hotel group'>Hotel group</Link></li>
        </ul>
        <ul className='list-c1' id='list-c-en'>
            <h1><Link to='/en/rencaizhanlue' id=''>Talent Strategy</Link></h1>
           <li><Link to='/en/rencaizhanlue' id=''>Talent growth plan</Link></li>
           <li><Link to='/en/rencaizhanlue' id=''> New employee growth plan </Link></li>
           <li><Link to='/en/rencaizhanlue' id=''>Employee growth plan </Link></li>
           <li><Link to='/en/rencaizhanlue' id=''>Anager growth  plan</Link></li>
        </ul>
        <ul>
                <h1>Cotact Us</h1>
           <li>Technical support：feibianquanqiu.com</li>
           <li id='wechat-number' >Wechat official account：GLOBALTRAVELMALL</li>
           <li className='phone-number-en'>Group switchboard：028-65278321</li>
           <li className='share'> 
               <div className='share-to-en'>Share to:</div>
        <div  className="bdsharebuttonbox">
        <div className='weixin-c'>
        <div className="bds_weixin" id='bds_weixin' data-cmd="weixin" title="分享到微信"><div className='wx-img-c'><img className='wx-img' data-cmd="weixin" src={weixin} alt=''/></div>  </div></div><span  >Wechat</span>
        <div className='sina-c'><div  className="bds_tsina" id='bds_tsina' data-cmd="tsina" title="分享到新浪微博"><div className='sina-img-c'><img className='sina-img' data-cmd="tsina" src={xinlang} alt=''/></div> </div></div><span className="tsina-f">Sina</span>
        
      </div></li>
        </ul>
        </div>
        <div className="erweima-c-en"  > <div className='erweima'></div> <div className='erweima-font'>official account</div> </div>
        
       
    </div> 
   
    
    </footer>
         )
     }
 }
 export default Footer_en;


