import React, { Component } from 'react';
import Nav from '../../components/CN/Nav';
import $ from 'jquery';
import Footer from '../../components/CN/Footer'
class Rencaizhanlue extends React.PureComponent {
    componentWillMount(){
        document.title='人才战略-飞遍全球国际旅游集团';
    }
    componentDidMount(){
        $('.rczl-content3-m').mousemove(function(){
            $('.m1').css('color','#424242')
        })
        $('.rczl-content3-m1').mousemove(function(){
            $('.m1').css('color','rgb(171,92,62)')
        })
        $('.rczl-content3-m1').mouseleave(function(){
            $('.m1').css('color','#424242')
        })
        //下滑
        $('.rczl-content5-m').on('click',function(event){
            let target=event.target;
            switch (target.id) {
            case 'rczl-content5-m1':
            $('.job1').text('销售经理')
            $('.rczl-content5-1').slideToggle('slow')
          
                
                $('.rczl-content5-triangle1').css({
                 
                    'borderWidth':'0 10px 10px  ',
                    'margin':'10px auto',
                    'borderColor':'   transparent transparent white'
                     
                })
                target.id='rczl-content5-m1-1' 
            break;
            case 'rczl-content5-m2':
            
            $('.rczl-content5-2').slideToggle('slow')
            
                
            $('.rczl-content5-triangle2').css({
             
                'borderWidth':'0 10px 10px  ',
                'margin':'10px auto',
                'borderColor':'   transparent transparent white'
                 
            })
            target.id='rczl-content5-m2-1'
            break;
            case 'rczl-content5-m3':
            
            $('.rczl-content5-3').slideToggle('slow')
            
                
            $('.rczl-content5-triangle3').css({
             
                'borderWidth':'0 10px 10px  ',
                'margin':'10px auto',
                'borderColor':'   transparent transparent white'
                 
            })
            target.id='rczl-content5-m3-1'
            break;
         //上滑
            case 'rczl-content5-m1-1':
            
            $('.rczl-content5-1').slideToggle('slow')
            $('.rczl-content5-triangle1').css({
                'borderWidth':' 10px 10px  0',
                'margin':'13px auto',
                'borderColor':' white  transparent transparent '
            })
            $('.job1').text('Air ticket Department')
            target.id='rczl-content5-m1'
            break;
            case 'rczl-content5-m2-1':
           
            $('.rczl-content5-2').slideToggle('slow')
            $('.rczl-content5-triangle2').css({
                'borderWidth':' 10px 10px  0',
                'margin':'13px auto',
                'borderColor':' white  transparent transparent '
            })
            target.id='rczl-content5-m2'
            break;
            case 'rczl-content5-m3-1':
           
            $('.rczl-content5-3').slideToggle('slow')
            $('.rczl-content5-triangle3').css({
                'borderWidth':' 10px 10px  0',
                'margin':'13px auto',
                'borderColor':' white  transparent transparent '
            })
            target.id='rczl-content5-m3'
            break;
            default:;
            }
        })
        
            
            
           
            
        
    }
    render(){
        return(
            <div>
                <Nav/>

                <div className='rencaizhanlue-c'>
                <div className='rencaizhanlue-bgt'>
                <div><h1>Strategic partners</h1><h2>International tourism group</h2></div>
                </div>
    <div className='rencaizhanlue-content-c'>
                
        <div className='rencaizhanlue-title'>
            <h1>人才战略</h1>
            <span className='title-xian'></span>
            <span>Talent Strategy</span>
            <span className='title-xian'></span>
        </div>
        <p className='rczl-content1'>飞遍全球国际旅游集团以奋斗者为本，通过长期艰苦奋斗，共同构建利益共同体,事业共同体和命运共同体<br/>集团将人才引进和培养作为长期发展战略，建立市场化有竞争力的薪酬福利体系，提供针对性的职业培训，健全富有竞争性和有活力<br/>的选拔任用制度，培养一支国际化、专业化、多元化的人才队伍!
</p>
        <div className='rczl-content2'>
       
            <div className='rczl-content2-aside'>
                <div className='rczl-content2-a'>
                <h1>企业使命</h1>
                <p>让旅行变得更舒适</p>
               <div className='rczl-round'> <div className='rczl-jt'></div></div>
            </div>
                        <div className='rczl-content2-a'>
                            <h1>企业愿景</h1>
                            <p>成为中国领先的国际旅游集团之一</p>
                            <div className='rczl-round'>   <div className='rczl-jt'></div></div> 
                    </div>
                
            
            <div className='rczl-content2-a'>
                <h1>企业核心</h1>
                <p>以客户为中心，以奋斗者为本员工与企业共同成长</p>
                <div className='rczl-round'>  <div className='rczl-jt'></div></div> 
               </div>
               </div>
               
               <div className='rczl-content2-img'></div>
               
        </div>
        <div className='rczl-content3'>
        <div className='rczl-content3-nav'>
        <div className='rczl-content3-nav1'>
            <h1>Talents Schem</h1>
            <h2>人才计划</h2>
            </div>
            <div className='rczl-content3-nav2'><span>人才战略</span><span>人才计划</span></div>
        </div>
       <div className='rczl-content3m-c'>
        <div className='rczl-content3-m1 ' >
            <h3 className='m1' id='m1'>人才成长计划</h3><h4 className='m1' id='m1'>Growth of People</h4><p>人才是企业成长和进步的基石，培训是员工成长和进步的基石。飞遍全球人才成长计划分为新员工成长计划、在职员工成长计划和管理人员成长计划。</p>
        </div>
        <div className='rczl-content3-m'><h3>新员工成长计划</h3><h4>Growth of new employees</h4>
        <p>培训是员工成长和进步的基石。新员工成长计划分为培训部和直属领导培训，帮助新员工理解业务和行业知识，并快速融入团队。</p>
        </div>
        <div className='rczl-content3-m'><h3>在职员工成长计划</h3><h4>Growth of staff</h4>
        <p>在职员工成长计划包括业务提升培训和管理专业知识培训，帮助在职员工提升业务水平，提供晋升机会。</p>
        </div>
        <div className='rczl-content3-m'> <h3>管理人员成长计划</h3><h4>Growth of Manager</h4>
        <p>管理人员成长计划主要帮助管理人员提升管理水平，提供晋升机会。</p>
        </div>
    </div>
    </div>
    <div className='rczl-content4'>
    <div className='rczl-content4-img2'></div>
        <div className='rczl-content4-m'>
            <h1>合伙人计划</h1>
          <span className='rczl-xian'></span><span className='rczl-content4-m-e'>Partner plan</span><span className='rczl-xian'></span>
            
            <p>飞遍全球国际旅游集团在合伙人模式上进行探索和创新<br/>提供“2+5”合伙人模式，即两大类型五种模式，可以满足各层次<br/>人才的需求。飞遍全球“超级合伙人”模式则将合伙人模式推向巅峰，完美诠释“以奋斗者为本”的核心价值观。未来还将推出<br/>&ensp;&ensp;“TUP(Time Unit Plan)+股权激励”机制，让企业永续发展。
</p>
        </div>
    </div>
    <div className='rczl-content5'>
        <h1>Recruitment post</h1>
        <div className='rczl-xian2'></div>
        <h3>请将您的简历资料发送到&ensp;hr@feibianquanqiu.com&ensp;并简要概括你的应聘岗位，我们将尽快与您联系。</h3>
        <div className='rczl-content5-m-c'>
           <div className='rczl-content5-m' id='rczl-content5-m1'>
            <h4 id='rczl-content5-m1' className='job1'>Air ticket Department</h4>
            <div id='rczl-content5-m1'  className='rczl-content5-i'>
           <div className='rczl-content5-triangle1' id='rczl-content5-m1'></div>
           </div>
           <div id='rczl-content5-m1-1' className='rczl-content5-1'>
            <div>岗位职责</div>
           
           
           </div>
           </div>



            <div className='rczl-content5-m'  id='rczl-content5-m2'>
                <h4 id='rczl-content5-m2' className='job2'>Tourism Department</h4>
                <div id='rczl-content5-m2' className='rczl-content5-i'>
                <div className='rczl-content5-triangle2' id='rczl-content5-m2'></div>
                </div>
                <div className='rczl-content5-2' id='rczl-content5-m2-1'>
                
                
                
                </div>
                </div>

            
                <div className='rczl-content5-m'  id='rczl-content5-m3'>
                <h4  id='rczl-content5-m3' className='job3'>Internationl Convention and Exhibition Department</h4>
                <div  id='rczl-content5-m3' className='rczl-content5-i'>
                <div   id='rczl-content5-m3' className='rczl-content5-triangle3'></div>
                </div >
                <div   id='rczl-content5-m3-1' className='rczl-content5-3'>
                
                
                
                </div>
                </div>
            
        </div>
    </div>
    </div>
    
                </div>
                <Footer/>
                </div>
        )
    }
}
export default Rencaizhanlue;


