//导航组件
import React, {Component} from 'react';
import sc from '../../image/搜索.svg'
import {NavLink} from 'react-router-dom';
import $ from 'jquery'
class Nav extends Component{
    
    
        componentDidMount(){
           $('.nav2-bg').click(function(){
               
               $('.nav2-c-c').hide();
           })
           $('.nav2-btn').click(function(){
               
            $('.nav2-c-c').toggle();
        })
        $(window).scroll(function(){
            var top = $(window).scrollTop();
            if(top>=200){
                $('.nav2').addClass('nav2-active');
                $('.nav2-btn').addClass('nav2-btn-active')
            }
            else{
                $('.nav2').removeClass('nav2-active');
                $('.nav2-btn').removeClass('nav2-btn-active')
            }
        })
     
    }
    
    render (){
        
        return(
            <div>
        <header className="header">
       
        <nav className="nav">
        <ul className='nav-c'>
       <div className='nav-1'>     
       <li><NavLink activeClassName='a-active2' to="/shouye" id='shouye'  >首页</NavLink></li>
       <li><NavLink  activeClassName='a-active' to="/jituanchanye"id='jituanchanye'>集团产业</NavLink></li>
       <li><NavLink  activeClassName='a-active' to="/zhanluehuoban" id='zhanluehuoban' >战略伙伴</NavLink></li>
       </div>
        <li><div className="logo" ></div></li>
       <div className="nav-2">
       <li><NavLink activeClassName='a-active' to="/qiyedongtai" id='qiyedongtai' >企业动态</NavLink></li>
       <li><NavLink activeClassName='a-active' to="/rencaizhanlue" id='rencaizhanlue' >人才战略</NavLink></li>
       <li><NavLink activeClassName='a-active' to="/jiaoliuzhongxin" id='jiaoliuzhongxin' >交流中心</NavLink></li>
        </div>
       <li>
        
       </li>
        <li className="language" ><NavLink className="CN" to="/"  >CN</NavLink> <NavLink className="EN"  to="/en" >EN</NavLink> </li>
       
      
      
           </ul> 
       </nav>
    </header>
    <div className='nav2 ' >
        <button className='nav2-btn'>
            <div className='nav2-xian'></div >
            <div className='nav2-xian'></div >
            <div  className='nav2-xian'></div >
            </button>
    <div className='nav2-logo'>logo</div>
    <div className='nav2-sc'><img src={sc} alt='图片正在加载'/></div>
</div>
<div className='nav2-c-c '>
<ul className='nav2-c'>
            
       <li className='nav2-shouye-c'>   <NavLink     className='nav2-shouye'  to="/shouye" id='shouye'  >首页</NavLink></li>
       <li><NavLink   to="/jituanchanye"id='jituanchanye'>集团产业</NavLink></li>
       <li><NavLink   to="/zhanluehuoban" id='zhanluehuoban' >战略伙伴</NavLink></li>
       <li><NavLink   to="/qiyedongtai" id='qiyedongtai' >企业动态</NavLink></li>
       <li><NavLink   to="/rencaizhanlue" id='rencaizhanlue' >人才战略</NavLink></li>
       <li><NavLink   to="/jiaoliuzhongxin" id='jiaoliuzhongxin' >交流中心</NavLink></li>
    </ul>
        <div className='nav2-bg'></div>
        </div>
        </div>
        )
    }
}
export default Nav;


