//主页路由组件
import React, { Component } from 'react';
import {Switch,Route} from 'react-router-dom';


import Zhanluehuoban from '../../pages/中文/Zhanluehuoban';
import Homepage from '../../pages/中文/Homepage';
import Jituanchanye from '../../pages/中文/Jituanchanye';
import Qiyedongtai from '../../pages/中文/Qiyedongtai';
import Rencaizhanlue from '../../pages/中文/Rencaizhanlue';
import Jiaoliuzhongxin from '../../pages//中文/Jiaoliuzhongxin'
import Homepage_en from '../../pages/English/Homepage_en';
import Jituanchanye_en from '../../pages/English/Jituanchanye_en';
import Zhanluehuoban_en from '../../pages/English/Zhanluehuoban_en';
import Qiyedongtai_en from '../../pages/English/Qiyedongtai_en';
import Rencaizhanlue_en from '../../pages/English/Rencaizhanlue_en';
import Jiaoliuzhongxin_en from '../../pages/English/Jiaoliuzhongxin_en';
class Main extends Component{
    render(){
        return(
            
       <Switch>
           <Route exact path='/' render={() => <Homepage/>} />
           <Route exact path='/shouye' render={() => <Homepage/>} />
            <Route path="/jituanchanye"render={() => <Jituanchanye/>}/>
            <Route path="/zhanluehuoban" render={() => <Zhanluehuoban/>} />
            <Route  path='/qiyedongtai' render={() =>  <Qiyedongtai/>}/>
            <Route path='/rencaizhanlue' render={() => <Rencaizhanlue/>}/>
            <Route  path='/jiaoliuzhongxin' render={() => <Jiaoliuzhongxin/>}/>
            <Route exact path='/en' render={() => <Homepage_en/>} />
            <Route exact path='/en/shouye' render={() => <Homepage_en/>} />
            <Route path="/en/jituanchanye"render={() => <Jituanchanye_en/>}/>
            <Route path="/en/zhanluehuoban" render={() => <Zhanluehuoban_en/>} />
            <Route  path='/en/qiyedongtai' render={() =>  <Qiyedongtai_en/>}/>
            <Route path='/en/rencaizhanlue' render={() => <Rencaizhanlue_en/>}/>
            <Route  path='/en/jiaoliuzhongxin' render={() => <Jiaoliuzhongxin_en/>}/>
           
            </Switch>
         
        )
    }
}
export default Main;


