 import React, { Component } from 'react';
import {Switch,Route} from 'react-router-dom';

import Qydt1 from '../../../pages/English/企业动态/Qydt1';
import Qydt2 from '../../../pages/English/企业动态/Qydt2';
import Qydt3 from '../../../pages/English/企业动态/Qydt3';
import News1 from '../../../pages/English/新闻中心/News1';
import News2 from '../../../pages/English/新闻中心/News2';
import News3 from '../../../pages/English/新闻中心/News3';

import News5 from '../../../pages/English/新闻中心/News5';
import News4 from '../../../pages/English/新闻中心/News4';
import Honor1 from '../../../pages/English/新闻中心/Honor1';
import Corporateimage1 from '../../../pages/English/新闻中心/Corporateimage1';
import Corporateimage2 from '../../../pages/English/新闻中心/Corporateimage2';
import Corporateimage3 from '../../../pages/English/新闻中心/corporateimage3';


 class Luyou_en  extends Component {


    render(){
        return(
                <Switch>
                     
                <Route exact path='/en/qiyedongtai'  render={() =><Qydt1/> } />
                <Route    path='/en/qiyedongtai/news'  render={()=><Qydt1/>} /> 
                <Route    path='/en/qiyedongtai/Honor/honor1'  render={()=><Honor1/>} />
                <Route    path='/en/qiyedongtai/Honor'  render={()=><Qydt2/>} />
                <Route    path='/en/qiyedongtai/corporateimage'  render={()=><Qydt3/>} />
                <Route    path='/en/qiyedongtai/news1'  render={()=><News1/>} />
                <Route    path='/en/qiyedongtai/news2'  render={()=><News2/>} />
                <Route    path='/en/qiyedongtai/news3'  render={()=><News3/>} />
                <Route    path='/en/qiyedongtai/news4'  render={()=><News4/>} />
                <Route    path='/en/qiyedongtai/news5'  render={()=><News5/>} />
                <Route    path='/en/qiyedongtai/corporateimage1'  render={()=><Corporateimage1/>} />
                <Route    path='/en/qiyedongtai/corporateimage2'  render={()=><Corporateimage2/>} />
                <Route    path='/en/qiyedongtai/corporateimage3'  render={()=><Corporateimage3/>} />
               

                </Switch>

            
        )
    }
 }
 
 export  default Luyou_en ;




