//成都开航特拉微夫
import React, { Component } from 'react';
import {Link} from 'react-router-dom'
import News_nav from '../../../components/CN/企业动态/News_nav';
import $ from 'jquery'

class News1 extends React.PureComponent{
    componentWillMount(){
        document.title='成都直飞特拉维夫航线将开通，每周往返各两班-新闻动态-飞遍全球国际旅游集团官方网站';
    }
    componentDidMount(){
        $(window).scrollTop(600)
    }
      render(){
          return(
            <div className='news-c'>
    <News_nav/>
    <div className='news-content-c'>
    <div className='news-content'>
        <h1>成都直飞特拉维夫航线将开通,每周往返各两班</h1>
        <h2> 发布时间2018.05.28   内容来源：官方网站</h2>
        <p> 一条新的国际直飞航线即将拉近成都与以色列之间的距离。昨日，记者从市口岸与物流办、川航等相关单位获悉，成都直飞以色列第二大城市——特拉维夫航线计划于8月
中旬开通，届时 成都有望成为中国内地第三个、中西部地区首个直航以色列的城市。该航线开通将极大地促进我国中西部地区与以色列之间的经贸合作和交流往来。
    “川航将于今年7月29日正式开通成都—特拉维夫航线。”昨日，记者从“以色列计划”微信公众号了解到该信息，随后立即向市口岸与物流办、川航等相关单位求证，获
得回复称：7月29日是该航线初步计划的努力争取开航日期，但在航班时刻未审批下来之前，具体开航日期也无法确定，不过从目前情况看将调整至8月中旬开通该航线，“将
调整至8月15日左右开通该航线”。</p>
        <div className='map'></div>
        <p> 特拉维夫是以色列第二大城市，被认为是以色列最为国际化的经济中心，也被誉为以色列的文化之都。目前，中国内地与以色列直飞通航的城市只有北京和上海，广州也计划
于8月份开通直飞特拉维夫的航线。因此，如果成都如期开通直航特拉维夫航线，那么成都有望成为中国内地第三个、中西部首个直航以色列的城市。</p>
        <div className='news-img-1-1'></div>
        <p>目前，由于没有直飞航线，从成都出发前往以色列一般需要经过北京、上海或香港中转，加上中转时间最快的也需要大约17个小时，多数则是需要花费20多个小时，成都直
飞航线开通后将极大地缩短出行时间，让两地之间往来变得更加便捷、高效。据了解，川航的成都直飞以色列航线将采用空客A330双通道宽体客机执飞，每周往返各两班，初
拟于每周三、七执行。而根据去年12月中国民航局发布的航线公示信息，川航申请开通成都直飞特拉维夫往返客运航线，使用机型为A330或A350。据悉，川航最快将于7月底
左右接收首架A350客机，而按照惯例新客机接收后还需要在国内航线运营度过“磨合期”，然后才投入到国际航线运营。因此，开航初期极可能是A330客机执飞该航线。</p>
        <div className='news-img-1-2'></div>
    </div>
    
            </div>
            <div className='news-foot'>
            <div>
        <span>上一篇：</span>
        <Link to='/qiyedongtai/corporateimage3'id=''><span>&nbsp;2018飞遍全球国际旅游集团户外拓展训练</span></Link>
        </div>
       
        <div>
        <span>下一篇：</span>
        <Link to='/qiyedongtai/news2'id=''> <span>从俄罗斯到卡塔尔，卡塔尔航空要做世界杯的连接者</span></Link>
        </div>
    </div>
            </div>
          )
      }
}

export default News1;


