import React  from 'react';
import {Link} from 'react-router-dom'

import $ from 'jquery'
import Qydt_nav_en from '../../../components/En/\u4F01\u4E1A\u52A8\u6001/Qydt_nav_en';
class Qydt2 extends React.PureComponent{
    componentWillMount(){
        document.title='企业荣誉-企业动态-飞遍全球国际旅游集团官方网站';
    }
    componentDidMount(){
        $('#qydt-nav1').css(
            {
                'color':'#424242',
               'borderBottom':'none'
               });
        
        $('.qydt-nav').on('click','a',function(event){
            
           
             $('#qydt-nav1').css(
                 {
                     'color':'#424242',
                    'borderBottom':'none'
                    });
                
           
        })
    }
    render(){
        return(
            
            <div>
                
            <Qydt_nav_en/> 
    <div className='qydt-content'>
     
        <div className='qydt-c'>
          <Link to='/'>  <div className='qydt-img-Honor'></div></Link>
          <Link to='/'>   <div className='qydt-font'>
          <h1>飞遍全球旅游集团获卡塔尔航空“最佳代理人”</h1>
          <p>2017年年底卡塔尔航空最佳代理颁奖典礼上，我司以机票销售评选为北亚区“最佳代理人”，过去的一年在团队的努力下，我们的业绩和服务得到了卡塔尔航空和客人的认
可，我们要做的不止是这些，在接下来的每一年，我们将以这个目标再接再厉，为全国各地的数百家中小型旅行社提供专业国际机票服务，满足数万中高端人士的出行需求。我
们有着优质的专业国际机票资源可以帮助中小旅行社提升市场份额和产品竞争力，有利于中小旅行社的快速成长。</p></div></Link>
            <div className='qydt-time-c'><div> <h1>07-18</h1><h2>2018</h2> </div><Link to='/'>查看更多</Link></div>
        </div>
        
        
    </div>
</div>
        )
    }
    }
    export default Qydt2;


