//页底组件
import React, { Component } from 'react';
import {Link} from 'react-router-dom';
import xinlang from '../../image/xinlang.svg'
import weixin from '../../image/weixin.svg';
import $ from 'jquery'
 class Footer extends Component {
     componentDidMount(){
         $('a.list-c-c').on('click',function(){
             window.location.reload();
         })
         $('.list-c').on('click' ,'h1',function(){
            $(window).scrollTop(50);
         })
         $('.list-c1').on('click' ,'h1',function(){
            $(window).scrollTop(50);
         })
         $('.list-c1').on('click' ,'li',function(){
            $(window).scrollTop(1600);
         })
         $('.list-c').on('click' ,'li',function(event){
             let target=event.target;
             switch (target.id) {
                 case 'International shipping':
                 $(window).scrollTop(600)
                 break;
                 case 'International tourism':
                 $(window).scrollTop(1200)
                 break;
                 case 'International exhibition':
                 $(window).scrollTop(1800)
                 break;
                 case 'Online voyage':
                 $(window).scrollTop(2500)
                 break;
                 case 'Cruise mall':
                 $(window).scrollTop(3200)
                 break;
                 case 'Airlines':
                 $(window).scrollTop(1000)
                 break;
                 case 'Tourism Bureau':
                 $(window).scrollTop(1800);
                 break;
                 case 'Hotel group':
                 $(window).scrollTop(2900)
                 break;
                 default:;
             }
            
         })
       
     }
     render (){
         return(
<footer className='footer'>
            <div className='footer-c'>
        <div className='list-c-c'> 
        <ul className='list-c'>    
            <h1><Link to='/jituanchanye' id='Group industry'>集团产业</Link></h1>
            <li><Link to='/jituanchanye' id='International shipping'>国际航运</Link></li>
            <li><Link to='/jituanchanye' id='International tourism'>国际旅游</Link></li>
            <li><Link to='/jituanchanye' id='International exhibition'>国际会展</Link></li>
            <li><Link to='/jituanchanye' id='Online voyage'>在线航旅</Link></li>
            <li><Link to='/jituanchanye' id='Cruise mall'>航旅MALL</Link></li>
        </ul>
        <ul className='list-c'>
           <h1><Link to='/zhanluehuoban'>合作伙伴</Link></h1>
           <li><Link to='/zhanluehuoban' id='Tourism Bureau'>旅游局</Link></li>
           <li><Link to='/zhanluehuoban' id='Airlines'>航空公司</Link></li>
           <li><Link to='/zhanluehuoban' id='Hotel group'>酒店集团</Link></li>
        </ul>
        <ul className='list-c1' >
            <h1><Link to='/rencaizhanlue'>人才战略</Link></h1>
           <li><Link to='/rencaizhanlue' id=''>人才成长计划</Link></li>
           <li><Link to='/rencaizhanlue' id=''>新员工成长计划</Link></li>
           <li><Link to='/rencaizhanlue' id=''>在职员工成长计划</Link></li>
           <li><Link to='/rencaizhanlue' id=''>管理人员成长计划</Link></li>
        </ul>
        <ul>
                <h1>联系我们</h1>
           <li>技术支持：feibianquanqiu.com</li>
           <li>微信公众号：GLOBALTRAVELMALL</li>
           <li>集团总机：028-65278321</li>
           <li className='share'> 
               <div className='share-to'>分享至:</div>
        <div  className="bdsharebuttonbox">
        <div className='weixin-c'>
        <div className="bds_weixin" id='bds_weixin' data-cmd="weixin" title="分享到微信"><div className='wx-img-c'><img className='wx-img' data-cmd="weixin" src={weixin} alt=''/></div>  </div></div><span  >微信</span>
        <div className='sina-c'><div  className="bds_tsina" id='bds_tsina' data-cmd="tsina" title="分享到新浪微博"><div className='sina-img-c'><img className='sina-img' data-cmd="tsina" src={xinlang} alt=''/></div> </div></div><span className="tsina-f">新浪微博</span>
        
      </div></li>
        </ul>
        </div>
        <div className="erweima-c"> <div className='erweima'></div> <div className='erweima-font'>集团官方公众号</div> </div>

        
       
    </div> 
   
    
    </footer>
         )
     }
 }
 export default Footer;


