 import React, { Component } from 'react';
import {Switch,Route} from 'react-router-dom';

import Qydt1 from '../../../pages/中文/企业动态/Qydt1';
import Qydt2 from '../../../pages/中文/企业动态/Qydt2';
import Qydt3 from '../../../pages/中文/企业动态/Qydt3';
import News1 from '../../../pages/中文/新闻中心/News1';
import News2 from '../../../pages/中文/新闻中心/News2';
import News3 from '../../../pages/中文/新闻中心/News3';

import News5 from '../../../pages/中文/新闻中心/News5';
import News4 from '../../../pages/中文/新闻中心/News4';
import Honor1 from '../../../pages/中文/新闻中心/Honor1';
import Corporateimage1 from '../../../pages/中文/新闻中心/Corporateimage1';
import Corporateimage2 from '../../../pages/中文/新闻中心/Corporateimage2';
import Corporateimage3 from '../../../pages/中文/新闻中心/corporateimage3';


 class Luyou extends Component {


    render(){
        return(
                <Switch>
                     
                <Route exact path='/qiyedongtai/'  render={() =><Qydt1/> } />
                <Route    path='/qiyedongtai/news'  render={()=><Qydt1/>} /> 
                <Route    path='/qiyedongtai/Honor/honor1'  render={()=><Honor1/>} />
                <Route    path='/qiyedongtai/Honor'  render={()=><Qydt2/>} />
                <Route    path='/qiyedongtai/corporateimage'  render={()=><Qydt3/>} />
                <Route    path='/qiyedongtai/news1'  render={()=><News1/>} />
                <Route    path='/qiyedongtai/news2'  render={()=><News2/>} />
                <Route    path='/qiyedongtai/news3'  render={()=><News3/>} />
                <Route    path='/qiyedongtai/news4'  render={()=><News4/>} />
                <Route    path='/qiyedongtai/news5'  render={()=><News5/>} />
                <Route    path='/qiyedongtai/corporateimage1'  render={()=><Corporateimage1/>} />
                <Route    path='/qiyedongtai/corporateimage2'  render={()=><Corporateimage2/>} />
                <Route    path='/qiyedongtai/corporateimage3'  render={()=><Corporateimage3/>} />
               

                </Switch>

            
        )
    }
 }
 
 export  default Luyou;




