//轮播组件
import React, { Component } from 'react';
import {Link} from 'react-router-dom';


class Carousel extends Component {
    render (){
        return(
          
        
        <div id="myCarousel" className="carousel slide">
        <ol className="carousel-indicators">
        <li  data-target="#myCarousel" data-slide-to="0" className="active"></li>
        <li   data-target="#myCarousel" data-slide-to="1"></li>
        <li   data-target="#myCarousel" data-slide-to="2"></li>
         <li  data-target="#myCarousel" data-slide-to="3"></li>
    </ol>
    {/*轮播项目*/}
    <div className="lunbotu">
    <div className="carousel-inner">
        
        <div  className=" item active carousel1" >
        <Link to='/jituanchanye'>
        <div  className="carousel-caption" id="carousel1">
        <div className='content-img1'></div>
        </div>
        </Link>
      </div>
      
      <div className=" item carousel2">
      
      <div  className="carousel-caption" id="carousel2"> 
      <h1 className="Introduction">专业的移动商务平台国际航旅综合服务商</h1>
        <div className="contanier1">
        <div className="content1 "><div><div className="icon"></div><div className="exp"><h1 className="number7">7</h1><div className="exp-f">年行业经验</div></div></div> </div>
        <div className="content1 "><div><div className="icon"></div><div className="exp"><h1 className="number7">24</h1><div className="exp-f">小时快速响应</div></div></div> </div>
        <div className="content1"><div><div className="icon"></div><div className="exp"><h1 className="number7">7</h1><div className="exp-f">年行业经验</div></div></div> </div>
        <div className="advisory"><a id="advisory" href="http://www.css88.com/book/css/properties/positioning/z-index.htm">立即咨询</a></div>
      </div>
      </div>
      </div>
      <div  className="item carousel3">
     
      
      </div>
      <div  className="item carousel4">
     
      
      </div>
      </div>
     {/*轮播导航*/}
	<a className="left carousel-control visible-lg" href="#myCarousel" role="button" data-slide="prev">
	    <span className="glyphicon glyphicon-chevron-left" aria-hidden="true"></span>
	    <span className="sr-only">Previous</span>
	</a>
	<a className="right carousel-control visible-lg" href="#myCarousel" role="button" data-slide="next">
	    <span className="glyphicon glyphicon-chevron-right" aria-hidden="true"></span>
	    <span className="sr-only">Next</span>
	</a>
      </div>
      </div>
   
      
      )
    }
}
export default Carousel;


