//轮播组件
import React, { Component } from 'react';
import {Link} from 'react-router-dom';
import qydt1 from '../../image/企业动态1.png'
import qydt2 from '../../image/企业动态2.png'
import qydt3 from '../../image/企业动态3.png'
import qydt4 from '../../image/企业动态4.png'
import qydt5 from '../../image/企业动态5.png'


class Carousel2 extends Component {
    render (){
        return(
          
        <section  id='qydt-carousel'>
        <div id="myCarousel-qydt" className="carousel slide">
        <ol className="carousel-indicators" >
        <li  data-target="#myCarousel-qydt" data-slide-to="0" className="active"></li>
        <li   data-target="#myCarousel-qydt" data-slide-to="1"></li>
        <li   data-target="#myCarousel-qydt" data-slide-to="2"></li>
         <li  data-target="#myCarousel-qydt" data-slide-to="3"></li>
         <li  data-target="#myCarousel-qydt" data-slide-to="4"></li>
    </ol>
    {/*轮播项目*/}
    <div className="lunbotu">
    <div className="carousel-inner">
        
     <div  className=" item active" id='carousel-qydt' >
     <Link to='/qiyedongtai/news2' id=''>
     <img src={qydt1}  className='carousel-qydt'/> 
        
        <div id='news-font'>
            <span >2022年卡塔尔世界杯，期待相见...</span>
            <div className='news-text'>
            <p>随着2018年俄罗斯世界杯落幕，全世界目光
              将聚集于2022年卡塔尔世界杯！这也是世界
              杯第一次在中东国家举办。卡塔尔世界杯...</p>
              </div>
            </div>
        
        </Link>
      </div>
      
      <div className=" item " id='carousel-qydt'>
      <Link to='/qiyedongtai/news1' id=''>
      <img src={qydt2} className='carousel-qydt '/> 
       
        <div id='news-font'>
            <span >新航线！川航带你直飞特拉维夫...</span>
            <p>成都——特拉维夫即将开航啦~11小时带你直飞
特拉维夫，体验这个极具人情味的城市。如果说
耶路撒冷是以色列的宗教中心，那么特拉维夫...</p>
            
        </div>
      
      </Link>
      </div>
       <div  className="item" id='carousel-qydt'>
     
       <Link to='/qiyedongtai/corporateimage1' id=''>
       <img src={qydt3} className='carousel-qydt '/> 
       <div id='news-font'>
                <span >飞遍全球国际旅游集团正式上线！</span>
            <p>飞遍全球国际旅游集团 是中国（四川）自由贸易试验区设立以来注册的首家国际旅游集团企业，总部位于成都金融城，飞遍全球旅游集团...</p>
                </div>
        </Link>
      </div> 
     <div  className="item " id='carousel-qydt'>
     
     <Link to='/qiyedongtai/corporateimage3' id=''>
     <img src={qydt4} className='carousel-qydt '/> 
     <div id='news-font'>
                <span >飞遍全球国际旅游集团“夏日团建”</span>
            <p>2018年7月21日，根据公司组织安排飞遍全球国际旅游集团全体人员，前往成都市双流县黄龙溪拓展基地，进行户外拓展训练...</p>
                </div>
      </Link>
      </div> 
      <div  className="item" id='carousel-qydt'>
     
      <Link to='/qiyedongtai/news5' id=''> 
      <img src={qydt5} className='carousel-qydt '/> 
      <div id='news-font'>
                <span >2018年开启”土耳其旅游年 ...</span>
            <p>土耳其是世界上最受欢迎的旅游目的地之一，作为
欧亚大陆之间的桥梁，土耳其为游人展现了令人惊
叹的自然风光、独特的历史和考古遗迹...</p>
                </div>
      </Link>
     </div>
      </div>
 
      </div>
      </div>
      </section>
      
      )
    }
}
export default Carousel2;


