import React from 'react';
import {NavLink} from 'react-router-dom';

const navstyle={
    color:'rgb(171,92,62)',
    borderBottom: '7px solid rgb(171,92,62)'

}
class Qydt_nav2 extends React.PureComponent{
   
    render(){
        return(
            <div className='qydt-nav' id='qydt-nav-en'>
            <NavLink id='qydt-nav1' style={navstyle} activeClassName='qydt-nav-active' to='/en/qiyedongtai/news' >Information</NavLink>
            <NavLink id='qydt-nav2'  activeClassName='qydt-nav-active' to='/en/qiyedongtai/Honor/honor1'>Honor</NavLink>
            <NavLink id='qydt-nav3'   activeClassName='qydt-nav-active' to='/en/qiyedongtai/corporateimage'>corporate image</NavLink>
        </div>
    )
    }
}
export default Qydt_nav2;


