import React  from 'react';
import {Link} from 'react-router-dom'
import Qydt_nav from '../../../components/CN/企业动态/Qydt_nav'
import $ from 'jquery'

class Qydt3 extends React.PureComponent{
    componentWillMount(){
        document.title='企业形象-企业动态-飞遍全球国际旅游集团官方网站';
    }
    componentDidMount(){
       
        $('#qydt-nav1').css(
            {
                'color':'#424242',
               'borderBottom':'none'
               });
        
        $('.qydt-nav').on('click','a',function(event){
         
           
             $('#qydt-nav1').css(
                 {
                     'color':'#424242',
                    'borderBottom':'none'
                    });
                
           
        })
    }
    render(){
        return(
            <div>
            <Qydt_nav/> 
    <div className='qydt-content'>
        <div className='qydt-c'>
          <Link id='' to='/qiyedongtai/corporateimage1'>  <div className='qydt-img-xx1'></div></Link>
          <Link id='' to='/qiyedongtai/corporateimage1'>   <div className='qydt-font'>
          <h1>飞遍全球航旅集团的历程发展和未来方向</h1>
          <p>遍全球国际旅游集团率先提出“品质出行”倡议，以产品的品质为核心，不断提高产品的设计能力和服务能力，先后开辟四川、重庆、贵州、云南、陕西、宁夏、甘肃等中
国西部旅游市场。并逐步发展成为中国西部地区领先的中高端国际旅游服务企业...</p>
          </div></Link>
            <div className='qydt-time-c'>
            <div> 
                <h1>06-06</h1><h2>2018</h2> </div>
            <Link id='' to='/qiyedongtai/corporateimage1'>查看更多</Link></div>
        </div>
        <div className='qydt-c'>
        <Link id='' to='/qiyedongtai/corporateimage2'> <div className='qydt-img-xx2'></div></Link>
        <Link id='' to='/qiyedongtai/corporateimage2'>    <div className='qydt-font'>
        <h1>飞遍全球旅游集团董事长亲自考察象岛旅游项目</h1>
        <p>2018年旅游发展迅速，旅游产品设计也在不断创新，飞遍全球旅游集团董事长亲自带领考察团队前往泰国象岛做考察，为旅游产品更新做湿地考察，从而在旅游服务上做得更专业...</p>
        </div></Link>
                <div className='qydt-time-c'>
                 <div><h1>07-18</h1><h2>2018</h2></div><Link id='' to='/qiyedongtai/corporateimage2'>查看更多</Link></div>
        </div>
        <div className='qydt-c3'>
        <Link id='' to='/qiyedongtai/corporateimage3'> <div className='qydt-img-xx3'></div></Link>
        <Link id='' to='/qiyedongtai/corporateimage3'> <div className='qydt-font'>
        <h1>2018飞遍全球国际旅游集团户外拓展训练</h1>
        <p>2018年7月21日，根据公司组织安排飞遍全球国际旅游集团全体人员，前往成都市双流县黄龙溪拓展基地，进行户外拓展训练。这是公司为了今后更好地发展，增加公司职
员之间凝聚力与向心力而举行的一次活动，得到了公司上下层的普遍支持与响应...
            </p></div></Link>
                <div className='qydt-time-c'> <div> <h1>07-21</h1><h2>2018</h2></div><Link id='' to='/qiyedongtai/corporateimage3'>查看更多</Link></div>
        </div>
        
    </div>
    </div>
        )
    }
    }
    export default Qydt3;
    



