//2018飞遍全球国际旅游集团户外拓展训练
import React from 'react';
import {Link} from 'react-router-dom';
import $ from 'jquery';
import News_nav from '../../../components/CN/企业动态/News_nav';
class Corporateimage3 extends React.PureComponent{
    componentWillMount(){
        document.title='2018飞遍全球国际旅游集团户外拓展训练-企业动态-飞遍全球国际旅游集团官方网站';
    }
    componentDidMount(){
        $(window).scrollTop(600)
    }
    
      render(){
          return(
            <div className='news-c'>
    <News_nav/>
    <div className='news-content-c'>
    <div className='news-content'>
        <h1>2018飞遍全球国际旅游集团户外拓展训练</h1>
        <h2> 发布时间2018.07.21   内容来源：官方网站</h2>
        <p>  2018年7月21日，根据公司组织安排飞遍全球国际旅游集团全体人员，前往成都市双流县黄龙溪拓展基地，进行户外拓展训练。这是公司为了今后更好地发展，增加公司职
员之间凝聚力与向心力而举行的一次活动，得到了公司上下层的普遍支持与响应。活动当天全员热像高涨，早上8点集合从公司地点环球中心出发，一路欢声笑语，放声高歌。到达训练基地，教练告诉大家此次拓展主要是通过户外活动集体完成目标，考验的是团队协作能力。</p>
        <div className='corporate-img-3-1'><img src=''/></div>
        <p> 活动项目一：组建团队，展示队长、队旗、队歌、口号，目的是打破人与人之间的隔阂，激发队员热情兴奋度；转变思维模式，认知培训目的以空杯心态投入培训，制造竞争
气氛，激发团队；初步建立热爱团队、崇尚荣誉、追求卓越的团队目标文化；在破冰中营造团队文化，达成有效的竞争共赢意识；PK制，增加队员的参与度，对团队的重视。
    活动项目二：水到渠成，队员手持凹槽水管，一字排开，一端放水，队员用槽管接力运输水到终点的桶里，限定时间水最多的一组获胜，目的是培养团队成员协作能力以及在
任务中发现问题解决问题如何提高工作效率的能力；增进人与人之间的关系；体会融入团队合作成功的成就感。</p>
       
        <div className='corporate-img-3-2'><img src=''/></div>
            
            
        <p>活动项目三：兄弟奔跑前进，队长拿到密令包裹后香队员传达讯息，并带领队员完成密令中的任务，任务的范围在黄龙溪古镇内，限定时间完成任务并且全员回到出发地，任
务完成率按积分制增加，主要目的考验团队执行力、团队协作能力、队长的引导能力，通过完成任务提高团队的凝聚力团结精神。</p>
        <div className='corporate-img-3-3'><img src=''/></div>
            <p>整个活动的项目完成的效果很好，活动结束后全体合影留念，记录下每一个热血的身影。参与此次拓展训练集体反响很大，平时很少交流的同事在此次活动中增加了联系和友
谊，团队协作精神得到提高，在以后的工作这份精神将继续发扬。</p>
</div>
    </div>
    <div className='news-foot'>
        <div>
        <span>上一篇：</span>
        <Link to='/qiyedongtai/corporateimage2' id=''><span>&nbsp;飞遍全球旅游集团董事长亲自考察象岛旅游项目</span></Link>
        </div>
        <div>
        <span>下一篇：</span>
        <Link to='/qiyedongtai/news1' id=''> <span>成都直飞特拉维夫航线将开通,每周往返各两班</span></Link>
        </div>
    </div>
            </div>
           
          )
      }
}

export default Corporateimage3


