//熊猫航线
import React, { Component } from 'react';
import {Link} from 'react-router-dom';
import $ from 'jquery'
import News_nav from '../../../components/CN/企业动态/News_nav';

class News3 extends React.PureComponent{
    componentWillMount(){
        document.title='A350助飞熊猫之路，四川航空开启“熊猫航线”-新闻动态-飞遍全球国际旅游集团官方网站';
    }
    componentDidMount(){
        $(window).scrollTop(600)
    }
      render(){
          return(
            <div className='news-c'>
    <News_nav/>
    <div className='news-content-c'>
    <div className='news-content'>
        <h1>A350助飞熊猫之路，四川航空开启“熊猫航线”!</h1>
        <h2>发布时间2018.07.08   内容来源：官方网站 </h2>
        <p>四川航空首架A350熊猫飞机将于2018年8月9日到达成都！8月16日进行商业首航。四川航空起飞三十载，从运七飞机探蜀道之难，到如今全空客机队铺就“熊猫之路”，一
步一印筑通途。270余条“熊猫航线”通达亚洲、欧洲、大洋洲和北美洲地区，在服务好国内需求的基础上，向“全服务、网络型、国际化”进一步转型升级。 宽体新时代，再
启新航程。开航三十年之际，川航引进首架A350XWB飞机。海阔天空，一起飞享世界的每一个角落。 </p>
        <div className='news-img-3-1'></div>
        <p>    飞遍全球航旅集团作为四川航空的合作伙伴，此次的“熊猫航线”我们将在线提供航班，有需求的合作旅行社以及出行需要的客人，可在我们官方商城咨询下单。 </p>
        <div className='news-img-3-2'><img src=''/></div>
        
    </div>
    
            </div>
            <div className='news-foot'>
        <div>
        <span>上一篇：</span>
        <Link to='/qiyedongtai/news2'id=''><span> &nbsp;从俄罗斯到卡塔尔，卡塔尔航空要做世界杯的连接者</span></Link>
        </div>
        <div>
        <span>下一篇：</span>
        <Link to='/qiyedongtai/news4'id=''> <span>摩洛哥免签吸引了更多华人，促进文化交流</span></Link>
        </div>
    </div>
            </div>
          )
      }
}

export default News3;


