import React, { Component } from 'react';
import Nav_en from '../../components/En/Nav_en';
import Footer_en from '../../components/En/Footer_en'
import {Link} from 'react-router-dom';
class Jituanchanye_en extends Component {
    componentWillMount(){
        document.title='集团产业-飞遍全球国际旅游集团官方网站';
    }
    render(){
        return(
            <div>
            <Nav_en/>
         
            <div className="Jituanchanye-c-c">
            <div className='jtcy-bgt'><div><h1>Group Industry</h1><h2>International tourism group</h2></div></div>
            <div className="jituanchanye-c1">
        <div className='jtcy-content'>
            <div className='juxing'></div>
            <h1>国际航空业务</h1>
            <h2>中小旅行社专业国际机票服务商</h2>
            <p>飞遍全球国际旅游集团经过十多年的专业沉淀以及对行业的理解和强大的资源整合能力，让集团成为中国西部地区专业国际机票领域三大服务商之一。
                集团与阿提哈德航公司、卡塔尔航空公司、阿联酋航空公司、美国联合航空公司等数十家航空公司达成合作伙伴关系，国际航线遍及全球五大洲。每年为全国各地的数百家中小旅行社提供专业国际机票服务，满足数万中高端人士的出行需求。
                优质的专业国际机票资源可以帮助中小旅行社提升市场份额和产品竞争力，有利于中小旅行社的快速成长。</p>
            </div>
           
        <div className='jituanchanye-img1'></div>
        <div className='jtcy-content-f'><span className='jtcy-xian'></span><span>International aviation business</span></div>
       
    </div>
    <div className="jituanchanye-c2">
    <div className='jtcy-img-c'> <div className='jituanchanye-img2'></div></div>
            <div className='jtcy-content'>
                <h1>国际旅游业务</h1>
                <h2>International tourise business</h2>
                <p>飞遍全球国际旅游集团以国际航空产业为基础，深度整合境外接待服务等出境游产业链各要素资源，是出境旅游产品的制造者和服务的提供者，产品涵盖亚洲、非洲、大洋洲、中东、地中海等全球主要的目的地国家和地区。目前是中东、地中海、非洲、日韩、澳新、美加旅游线路的主要服务商之一。
集团与各国驻成都总领事馆、各国旅游局、境内外地接社、国际酒店集团等保持良好合作关系。</p>
                <Link to='/en/jiaoliuzhongxin'>More →</Link>
                </div>
        
    </div>
    <div className="jituanchanye-c3">
        <div className='jtcy-content'>
            <div className='juxing'></div>
            <h1>国际会展业务</h1>
            <h2>全国出境国际会展服务中心</h2>
            <p>飞遍全球国际旅游集团在成都设立全国出境国际会展服务中心，为全国的出境会展企业提供欧洲、北美以及会展新兴国家和地区的国际航空和国际旅游资源。重点开发以北京为中心的京津冀区域市场，以上海为中心的长三角区域市场，以广州为中心珠三角区域市场。同时帮助中国二三线区域中心城市和重点地区的国际会展商跨出国门，走向世界。
会展业是现代服务业的重要支柱，被世界各国所重视。随着经济全球化水平的不断提升，会展行业在促进贸易往来、技术交流、信息沟通、经济合作及增加就业等方面发挥着日益重要的作用。中国会展业可以借“走出去”培育自己的品牌、提升自己的水平，以及确立新的全球合作伙伴关系。
</p>
            </div>
        <div className='jituanchanye-img3'></div>
        <div className='jtcy-content-f'><span className='jtcy-xian'></span><span>International aviation business</span></div>
    </div>
    <div className="jituanchanye-c4">
    <div className='jtcy-img-c'>  <div className='jituanchanye-img4'></div></div>
            <div className='jtcy-content'>
                <h1>在线航旅业务</h1>
                <h2>Online Air Travel Service</h2>
                <p>飞遍全球国际旅游集团以科技为先导，通过互联网技术自建分销系统，将全球核心旅游资源进行整合并分销到各大在线及线下渠道。系统拥有专业收集入境市场整体数据、入境区域数据分析、客户数据分析、航线入境乘客数量变化数据分析能力，为客户提供更有竞争力的价格和服务。</p>
                <Link to='/en/jiaoliuzhongxin'>More →</Link>
                </div>
      
    </div>
    <div className="jituanchanye-c5">
    <div className='jtcy-content'>
            <div className='juxing'></div>
                <h1>飞遍全球航旅MALL</h1>
                <h2>集国际旅游、国际机票、特色商品为一体的综合服务商城</h2>
                <p>飞遍全球航旅MALL是集国际旅游产品、国际机票和旅游目的地特色商品为一体的综合服务商城。为客户提供有品质的，舒适度更高的热门国际旅游产品。同时与多家国际航空公司达成合作关系，提供国际机票直销服务。</p>
                <Link to='/en/jiaoliuzhongxin'>More →</Link>
                </div>
        <div className='jituanchanye-img5'></div>
        <div className='jtcy-content-f'><span className='jtcy-xian'></span><span>International aviation business</span></div>
    </div>
</div>
<Footer_en/>
            </div>
        )
    }
}
export default Jituanchanye_en;


