//土耳其旅游
import React, { Component } from 'react';
import {Link} from 'react-router-dom'
import $ from 'jquery'
import News_nav from '../../../components/En/企业动态/News_nav';

class News5 extends React.PureComponent{
    componentWillMount(){
       
        document.title='企业动态-飞遍全球国际旅游集团官方网站';
    }
    componentDidMount(){
        $(window).scrollTop(600)
    }
      render(){
          return(
            <div className='news-c'>
   <News_nav/>
   <div className='news-content-c'>
    <div className='news-content'>
        <h1>2018开启土耳其旅游年</h1>
        <h2>发布时间2018.07.22   内容来源：海外旅游指南 </h2>
        <p> 2017年传遍大街小巷的《带你去旅行》，热门歌词“我想要带你去浪漫的土耳其”唤起了不少人的土耳其旅游梦，国内的各大真人秀节目如《花儿与少年》、《二十四小时》
等纷纷远赴土耳其取景。由于一带一路政策的推行，2018年更是直接成为中土旅游年</p>
        <div className='news-img-5-1'></div>
        <p>因为2018年是中土旅游年，赴土耳其游玩的中国游客福利增多。首先，自今年3月开始，中国公民申请土耳其电子签，可以使用支付宝进行支付，最快15分钟就能搞定土耳
其签证！单次可逗留30天，签证有效期为180天，土耳其，说走就走！除了签证福利，除了签证福利，土耳其航空还新增了许多从中国大陆直飞土耳其的航班，更开通了不少新
航线，2016年新增了重庆直飞航线，再加上北京、上海、广州、香港四个可直飞城市！ </p>
        <div className='news-img-5-2'><img src=''/></div>
        <p>我们都知道土耳其特别适合买买买，祖马龙香水、潘多拉手链倍儿便宜，今年内，土耳其的九成商户的pos终端都将开始支持银联卡，ATM的银联受理覆盖率也会提升，到伊
斯坦布尔、安卡拉、内夫谢希尔等旅游城市游玩都可以使用银联卡了！不仅是商户，当地的多家航空公司、旅游预订网站都已接受银联在线支付。</p>
        <div className='news-img-5-3'><img src=''/></div>
     
    </div>
   
            </div>
            <div className='news-foot'>
        <div>
        <span>上一篇：</span>
        <Link to='/en/qiyedongtai/news4'><span>&nbsp;摩洛哥免签吸引了更多华人，促进文化交流</span></Link>
        </div>
        <div>
        <span>下一篇：</span>
        <Link to='/en/qiyedongtai/Honor/honor1'> <span>飞遍全球旅游集团获卡塔尔航空“最佳代理人”</span></Link>
        </div>
        
    </div>
            </div>
          )
      }
}

export default News5;


