import React, { Component } from 'react';
import Nav from '../../components/CN/Nav';

import $ from 'jquery';
import Luyou from '../../components/CN/企业动态/Luyou';
import Footer from '../../components/CN/Footer'

class Qiyedongtai extends Component {

    componentWillMount(){
        document.title='企业动态-飞遍全球国际旅游集团官方网站';
    }
   
        componentDidMount(){
           
            $(window).scrollTop(600)
            
        }
      
    
    render(){
        return(
        <div>
            
           
            
            <Nav/>
            
            <div className='qiyedongtai-c'>
        <div className='qydt-bgt'>
            
             <div>
            <h1>NEWS</h1>
            <h2>International tourism group</h2>
        </div>
    </div>
    <div className='qydt-content-c'>
    
    <Luyou/>
  
   
    
        
       </div>
        
       </div>
       
       <Footer/>    
    </div> 
  

           
    )
    }
}


  

export default Qiyedongtai



