//导航组件
import React from 'react';

import {NavLink} from 'react-router-dom';

class Nav_en extends React.PureComponent{
    
    
        
     
     
    
    render (){
        
        return(
        <header className="header">
       
        <nav className="nav" id='nav-en'>
        <ul className='nav-c' id='nav-c-en'>
       <div className='nav-1'>     
       <li><NavLink  activeClassName='a-active' to="/en/shouye"  id='' >HOME </NavLink></li>
       <li><NavLink  activeClassName='a-active' to="/en/jituanchanye"id='jituanchanye'> INDUSTRY</NavLink></li>
       <li><NavLink  activeClassName='a-active' to="/en/zhanluehuoban" id='zhanluehuoban' > PARTNER</NavLink></li>
       </div>
        <li><div className="logo" ></div></li>
       <div className="nav-2">
       <li><NavLink activeClassName='a-active' to="/en/qiyedongtai" id='qiyedongtai' > DYNAMICS</NavLink></li>
       <li><NavLink activeClassName='a-active' to="/en/rencaizhanlue" id='rencaizhanlue' >TALENT</NavLink></li>
       <li><NavLink activeClassName='a-active' to="/en/jiaoliuzhongxin" id='jiaoliuzhongxin' >CONTACT</NavLink></li>
        </div>
       <li>
        
       </li>
        <li className="language" ><NavLink className="CN" to=""  >EN</NavLink> <NavLink className="EN"  to="/shouye" >CN</NavLink> </li>
       
      
      
           </ul> 
       </nav>
       
        </header>
        )
    }
}
export default Nav_en;


