//飞遍全球旅游集团董事长亲自考察象岛旅游项目
import React from 'react';
import {Link} from 'react-router-dom';
import $ from 'jquery';
import News_nav from '../../../components/En/企业动态/News_nav';

class Corporateimage2 extends React.PureComponent{
    componentWillMount(){
        document.title='飞遍全球旅游集团董事长亲自考察象岛旅游项目-新闻动态-飞遍全球国际旅游集团官方网站';
    }
    componentDidMount(){
        $(window).scrollTop(600)
    }
      render(){
          return(
            <div className='news-c'>
    <News_nav/>
    <div className='news-content-c'>
    <div className='news-content'>
        <h1>飞遍全球旅游集团董事长亲自考察象岛旅游项目</h1>
        <h2> 发布时间2018.07.18   内容来源：官方网站</h2>
        <p>  2018年旅游发展迅速，旅游产品设计也在不断创新，飞遍全球旅游集团董事长亲自带领考察团队前往泰国象岛做考察，为旅游产品更新做湿地考察，从而在旅游服务上做得更
专业。象岛(Koh chang)，不同于普吉、苏梅这些已经被开发得非常成熟的岛屿，可能是由于没有建机场的缘故，从曼谷前往象岛需要4小时的车程加1小时的轮渡，对于旅游者来
说，没有直达飞机，一方面阻碍了人山人海的旅游团前来，另一方面，却意外地保护了象岛世外桃源般的原生态热带小岛,家庭游、自由行非常棒的选择，飞遍全球旅游集团将开
启对象岛的全面考察，开辟世外桃源新路线。</p>
        <div className='corporate-img-2-1'><img src=''/></div>
        <p> 曼谷航空开通了从曼谷飞往达叻机场的航班，把原本到象岛4个小时颠簸的车程缩短到了45分钟，所以为了节省路上的时间和提高舒适度，首选曼谷航空。飞行时间都不到1
个小时，机上提供有餐食、饮料，而且还十分的丰富，有泰国特色的糯米饭等，分发完餐食之后，还提供咖啡喝中国茶。真的是十分的贴心，让旅客本就短途的飞行，感觉度过
的更快。</p>
       
        <div className='corporate-img-2-2'><img src=''/></div>
            
            
        <p> Awa Resort酒店位于著名的凯裴海滩区，地理位置便捷。酒店设计以中国元素居多，深受欧美旅客喜爱， 酒店拥有高品质的服务以及完善的设施，可满足游客们的所有需求。
 酒店内设施繁多，无线网络(公共区域), 停车场, 送餐服务, 酒店/机场接送, 儿童看护服务等都已配备，自由行推荐。</p>
        <div className='corporate-img-2-3'><img src=''/></div>
        </div>
    </div>
    <div className='news-foot'>
        <div>
        <span>上一篇：</span>
        <Link to='/en/qiyedongtai/corporateimage1'><span>&nbsp;飞遍全球航旅集团的历程发展和未来方向</span></Link>
        </div>
        <div>
        <span>下一篇：</span>
        <Link to='/en/qiyedongtai/corporateimage3'> <span>2018飞遍全球国际旅游集团户外拓展训练</span></Link>
        </div>
    </div>
            </div>
           
          )
      }
}

export default Corporateimage2;


